const VERSIONS = preval`
  const { readdirSync } = require('fs');
  const versionsContents = readdirSync('./pages/versions', {withFileTypes: true});
  const versions = versionsContents.filter(f => f.isDirectory()).map(f => f.name);
  module.exports = versions;
`;

const MENUS = preval`
  const dirTree = require("directory-tree");
  const tree = dirTree("./pages/versions" ,{
    extensions: /\.(md|directory)$/
  });


  let A = {};
  tree.children.forEach(e => {
    const group = [];
    e.children.forEach(f => {
     
        const c = [];
        if (f.children){
          f.children.forEach(k => {
            if (k.extension === ".md") {
              c.push({ name: k.name, path: k.path });
            }
          });
        }
       
        group.push({
          name: f.name,
          children: c.length ===0 ? null : c,
          path: f.path 
        });
      
    });
    A[e.name] = group;
  });

  module.exports = A;
`;

export { VERSIONS, MENUS };
