import styled, { keyframes, css } from "react-emotion";
import NextLink from "next/link";

import * as React from "react";
import * as Constants from "~/common/constants";

const STYLES_TITLE = css`
  display: block;
  position: relative;
  margin-bottom: 14px;
  line-height: 1.3rem;
  text-decoration: none;
  border-bottom-color: #ccc;
  font-family: ${Constants.fontFamilies.demi};
`;

const STYLES_ACTIVE = css`
  color: ${Constants.colors.expoLighter};

  :visited {
    color: ${Constants.colors.expo};
  }

  :hover {
    color: ${Constants.colors.expo};
  }
`;

const STYLES_DEFAULT = css`
  color: ${Constants.colors.black40};
  transition: 200ms ease color;

  :visited {
    color: ${Constants.colors.black40};
  }

  :hover {
    color: ${Constants.colors.expo};
    cursor: pointer;
  }
`;

class Arrow extends React.Component {
  render() {
    return (
      <i
        className={`fas fa-chevron-${this.props.isOpen ? "up" : "down"}`}
        style={{ position: "absolute", right: 0, top: 0 }}
      />
    );
  }
}

export default class DocumentationSidebarTitle extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: this.isSelected()
    };
  }

  isSelected() {
    if (!this.props.url) {
      return false;
    }

    const linkUrl = this.props.info.as || this.props.info.href;

    if (linkUrl === this.props.url.pathname || linkUrl === this.props.asPath) {
      return true;
    }

    return false;
  }

  _toggleIsOpen = () => {
    this.setState(({ isOpen }) => ({ isOpen: !isOpen }));
  };

  render() {
    console.log(this.props.info.path);
    if (!this.props.info.path) {
      return <div className={STYLES_TITLE}>{this.props.children}</div>;
    }

    return (
      <div
        className={`${STYLES_TITLE} ${
          this.isSelected() ? STYLES_ACTIVE : STYLES_DEFAULT
        }`}
      >
        <NextLink href={this.props.info.path}>
          <div>{this.props.children}</div>
        </NextLink>
      </div>
    );
  }
}
